.box {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.child-one {
  grid-column: 1 / 3;
  text-align: left;
}
.child-two {
  grid-column: 3 / 5;
  text-align: left;
}


/*  VIEW BOX & CHILDRENS */
.view-box {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  /* grid-auto-rows: 240px 240px; */
}
.view-child-one {
  background: -webkit-linear-gradient( white 50%, #e5fffc 50%);
  background: linear-gradient( white 50%, #e5fffc 50%);
  /* color: #4c4c64; */
  grid-column: span 7;
  padding-top: 3em;
  padding-bottom: 1em
}
.view-child-two {
  background-color: #e5fffc;
  grid-column: span 7;
  
}
.view-child-three {
  background-color: #ffffff;
  grid-column: span 7;
  padding-bottom: 1em;
}

